/**
 * Format price to Pakistani Rupees (PKR)
 * 
 * @param {number} price - The price to format
 * @returns {string} The formatted price in PKR
 */
export const formatPrice = (price) => {
  // Approximate conversion from USD to PKR (as of 2023)
  // In a real app, you would use an API for current conversion rates
  const pkrValue = price * 280;

  return `Rs. ${pkrValue.toLocaleString('en-PK')}`;
}; 