// pages/index.js
"use client"

import Image from 'next/image'
import Link from 'next/link'
import { useEffect, useState } from 'react'
import { useCart } from '@/context/CartContext'
import { formatPrice } from '@/utils/formatCurrency'

export default function Home() {
  const [showFeatured, setShowFeatured] = useState(false)
  const { addToCart } = useCart()

  useEffect(() => {
    const handleScroll = () => {
      const scrollPosition = window.scrollY
      const windowHeight = window.innerHeight
      if (scrollPosition > windowHeight * 0.3) {
        setShowFeatured(true)
      }
    }

    window.addEventListener('scroll', handleScroll)
    return () => window.removeEventListener('scroll', handleScroll)
  }, [])

  const featuredProducts = [
    {
      id: 1,
      name: 'Premium Dumbbell Set',
      price: 299.99,
      image: '/images/dumbbell.webp',
      category: 'Equipment',
    },
    {
      id: 2,
      name: 'Resistance Bands Pack',
      price: 49.99,
      image: '/images/bands.jpg',
      category: 'Accessories',
    },
    {
      id: 3,
      name: 'Whey Protein Powder',
      price: 59.99,
      image: '/images/protein.webp',
      category: 'Supplements',
    },
    {
      id: 4,
      name: 'Performance T-Shirt',
      price: 29.99,
      image: '/images/tshirt.jpeg',
      category: 'Clothing',
    },
  ];

  return (
    <main className="min-h-screen bg-white">
      {/* Hero Section */}
      <section className="relative h-screen flex items-center">
        <div className="absolute inset-0">
          <Image
            src="/images/background.png"
            alt="Gym background"
            fill
            sizes="100vw"
            className='blur-[2px]'
            priority
          />
          <div className="absolute inset-0" />
        </div>
        <div className="relative max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-24 text-center">
          <h1 className="text-4xl md:text-6xl font-bold text-white mb-6">
            Your Ultimate Fitness <span className="text-orange-400">Destination</span>
          </h1>
          <p className="text-xl text-gray-200 mb-8">
            Premium equipment and accessories for your fitness journey
          </p>
          <Link
            href="/products"
            className="inline-block bg-orange-400 text-teal-900 px-8 py-3 rounded-lg font-semibold hover:bg-orange-500 transition duration-300"
          >
            Shop Now
          </Link>
        </div>
      </section>

      {/* Featured Products */}
      <section
        className={`py-24 transform transition-all duration-1000 ${showFeatured ? 'translate-y-0 opacity-100' : 'translate-y-20 opacity-0'
          }`}
      >
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <h2 className="text-3xl font-bold text-teal-800 text-center mb-12">
            Featured Products
          </h2>
          <div className="grid grid-cols-1 md:grid-cols-4 gap-8">
            {featuredProducts.map((product) => (
              <div
                key={product.id}
                className="bg-white rounded-lg shadow-lg overflow-hidden border border-teal-100 hover:shadow-xl transition-shadow duration-300"
              >
                <div className="relative h-48">
                  <Image
                    src={product.image}
                    alt={product.name}
                    fill
                    className="object-cover"
                  />
                </div>
                <div className="p-4">
                  <span className="text-sm text-orange-500 font-medium">
                    {product.category}
                  </span>
                  <h3 className="text-lg font-semibold text-teal-800 mt-1">
                    {product.name}
                  </h3>
                  <p className="text-teal-900 font-semibold mt-2">
                    {formatPrice(product.price)}
                  </p>
                  <button
                    onClick={() => addToCart(product)}
                    className="block w-full mt-4 bg-orange-400 text-teal-900 py-2 text-center rounded-md hover:bg-orange-500 transition-colors duration-300"
                  >
                    Add to Cart
                  </button>
                </div>
              </div>
            ))}
          </div>
        </div>
      </section>
    </main>
  )
} 